<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Complex' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left"></el-divider>
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<div class="InBoxBody-con-input-style">
						<el-form-item label="" prop="valkm" style="float: left;">
							<el-cascader class="flat" clearable v-model="ruleForm.valkm" :options="optionskm" :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item>
						<!-- <el-form-item label="" prop="valls" style="float: left;">
							<el-cascader class="flat" clearable v-model="ruleForm.valls" :options="optionsls" :props="{ expandTrigger: 'hover' }"></el-cascader>
						</el-form-item> -->
						<el-form-item label="" prop="teacher_name" style="float: left;">
							<el-input class="flat" v-model="ruleForm.teacher_name" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="syntheticalcn_id" style="float: left;">
							<el-select v-model="ruleForm.syntheticalcn_id" placeholder="" class="flat">
							    <el-option v-for="item in optionsZHT" :key="item.value" :label="item.name" :value="item.id"></el-option>
							</el-select>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="" prop="c_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.c_num" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="present_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.present_price" placeholder=""></el-input>
						</el-form-item>
						<!-- <el-form-item label="" style="float: left;">
							<div class="InBoxBody-con-input-tit moenycor">{{(ruleForm.present_price/ruleForm.c_num).toFixed(2)}}</div>
						</el-form-item> -->
						<el-form-item label="" prop="original_price" style="float: left;">
							<el-input @change="intchan()" class="flat" v-model="ruleForm.original_price" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label=":" prop="surplus_num" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.surplus_num" placeholder=""></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="" prop="start_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.start_age" placeholder=":3"></el-input>
						</el-form-item>
						<el-form-item label="" prop="end_age" style="float: left;">
							<el-input @change="numberchan()" class="flat" v-model="ruleForm.end_age" placeholder=":8"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="" prop="title">
							<el-input class="flattextarea" type="textarea" :rows="10" placeholder="" v-model="ruleForm.title"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label=":">
							<el-input class="flattextarea" type="textarea" :rows="4" placeholder="" v-model="ruleForm.arrange_time"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="" prop="img">
							<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.img"
							 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
								<i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
								<img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</el-form-item>
						<div slot="tip" class="el-upload__tip">jpg/png400*400,2M</div>
					</div>
					<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-button style="margin-left:90px;width:80px;" type="warning" @click="submitForm('ruleForm')"> </el-button>
						<el-button style="width:80px;" type="warning" plain @click="$router.go(-1)"> </el-button>
					</div>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				optionskm: [], //
				optionsls: [], //
				optionsZHT:[],//
				ruleForm: {
					valkm: '',
					valls: '',
					teacher_name:'',
					arrange_time:'',
					start_age:'',
					end_age:'',
					surplus_num:'',
					syntheticalcn_id:'',
					c_num: '',
					present_price: '',
					original_price: '',
					title: '',
					img: []
				},
				rules: {
					valkm: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					teacher_name: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					valls: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					c_num: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					present_price: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					start_age: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					end_age: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					original_price: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					title: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					img: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				},
				//
				dialogImageUrl: '',
				dialogVisible: false,
			}
		},
		methods: {
			//
			intchan(){
			    this.ruleForm.present_price = this.MoneyYZ(this.ruleForm.present_price);
				this.ruleForm.original_price = this.MoneyYZ(this.ruleForm.original_price);
			},
			//
			numberchan(){
				this.ruleForm.c_num = this.mudYZ(this.ruleForm.c_num);
				this.ruleForm.surplus_num = this.mudYZ(this.ruleForm.surplus_num);
				this.ruleForm.start_age = this.mudYZ(this.ruleForm.start_age);
				this.ruleForm.end_age = this.mudYZ(this.ruleForm.end_age);
			},
			MoneyYZ(e) {
				var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (e!= "" && !reg.test(e)) {
					e = "";
				};
				return e
			},
			mudYZ(e) {
				var reg = /[^\d]/g;
				if (e!= "" && reg.test(e)) {
					e = "";
				};
				return e
			},
			//
			handleRemove(file, fileList) {
				this.ruleForm.img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 
			handsuccess(file, fileList) {
				this.ruleForm.img.push(fileList);
			},
			//
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						let data = this.ruleForm;
						let tjurl = 'v1/addjgSynthetical';
						if (that.$route.query.mid != undefined) {
							tjurl = 'v1/setjgSynthetical';
							data.course_id = that.$route.query.mid;
						}
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '',
									type: 'success'
								});
								that.$router.go(-1);
							};
						});
					} else {
						return false;
					}
				});
			},
			// 
			objkm() {
				let that = this;
				that.$post('v1/getjggroupCategoryCurriculum').then((response) => {
					if (response.code == 1000) {
						this.optionskm = response.data
					};
				});
			},
			objLS() {
				let that = this;
				that.$post('v1/getjgTeacherTypesearch').then((response) => {
					if (response.code == 1000) {
						this.optionsls = response.data
					};
				});
			},
			objZHT() {
				let that = this;
				that.$post('v1/getjgSyntheticalName').then((response) => {
					if (response.code == 1000) {
						this.optionsZHT = response.data
					};
				});
			},
			// 
			handleClose(done) {
				done();
			},
			// 
			ObjectCon() {
				let that = this;
				let data = {
					course_id: this.$route.query.mid,
				};
				that.$post('v1/getjgSyntheticaldetails', data).then((response) => {
					if (response.code == 1000) {
						response.data.present_price = response.data.present_price;
						this.ruleForm = response.data;
					};
				});
			}
		},
		mounted() {
			this.objkm();
			this.objLS();
			this.objZHT();
			// 
			//id
			if (this.$route.query.mid != undefined) {
				this.ObjectCon();
			};
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //
	@import './ComplexCOM/ComplexCon.less'; //
</style>
<style>
	.is-selected {
		color: #1989FA;
	}
</style>



