<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/IntroCon' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-divider content-position="left"></el-divider>
		<div class="InBoxBody-con">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<el-form-item label="logo(400*400,1M):" prop="logo">
					<el-upload :limit="1" action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.logo"
					 :on-preview="handlePictureCardPreviewlogo" :on-success="handsuccesslogo" :on-remove="handleRemovelogo">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisiblelogo">
						<img width="100%" :src="dialogImageUrllogo" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label=":" prop="remarks" style="width:calc(100% - 80px);">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder="200"></el-input>
				</el-form-item>
				<el-form-item label=":" prop="service_student" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.service_student" placeholder=":50"></el-input>
				</el-form-item>
				<el-form-item label=":" prop="found_time" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.found_time" placeholder=":2020/10/12"></el-input>
				</el-form-item>
				<el-form-item label=":" prop="kf_phone" style="width:calc(100% - 80px);">
					<el-input size="large" v-model="ruleForm.kf_phone" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="(,400*400,2M):" prop="wheel_img">
					<el-upload  action="https://zht.insooner.com/jg/v1/getUpdatePictures" multiple list-type="picture-card" :file-list="ruleForm.wheel_img"
					 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="" prop="introduction" style="width:calc(100% - 80px);">
					<Editor :serverUrl="imgUrl" :imgData="imgData" :detailCon="detailContent" @changeContent="changeContent" ref="editor"></Editor>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
					<el-button @click="resetForm('ruleForm')"></el-button>
					<el-button @click="$router.go(-1)"></el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import Editor from '../../../assets/Plugin/RichText.vue';
	export default {
		components: {
			Editor
		},
		data() {
			return {
				detailContent:'',
				//
				dialogImageUrl: '',
				dialogImageUrllogo: '',
				dialogVisible: false,
				dialogVisiblelogo: false,
				// 
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					wheel_img:[],// 
					logo:[],
					remarks:'',// 
					introduction:'',// 
					found_time:'',
					kf_phone:'',
					service_student:'',
				},
				rules: {
					remarks: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					kf_phone: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					found_time: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					service_student: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					introduction: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					wheel_img: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					logo: [{
						required: true,
						message: 'logo',
						trigger: 'blur'
					}]
				}
			}
		},
		methods: {
			// 
			changeContent(value){
				this.ruleForm.introduction = value;
			},
			//logo
			handleRemovelogo(file, fileList) {
				this.ruleForm.logo = fileList;
			},
			handlePictureCardPreviewlogo(file) {
				this.dialogImageUrllogo = file.url;
				this.dialogVisiblelogo = true;
			},
			// 
			handsuccesslogo(file, fileList) {
				this.ruleForm.logo.push(fileList);
			},
			//
			handleRemove(file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 
			handsuccess(url,file, fileList) {
				this.ruleForm.wheel_img = fileList;
			},
			//
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						// 
						let data = that.ruleForm;
						let tjurl = 'v1/setjgMemberinfo';
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '',
									type: 'success'
								});
								that.$storeState.logo = that.ruleForm.logo[0].url;
								that.$router.go(-1);
							};
						})
					} else {
						return false;
					}
				});
			},
			//
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			// 
			ObjectCon(){
				let that =this;
				that.$post('v1/getjgMemberinfo').then((response) => {
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.detailContent = response.data.introduction;
					};
				});
			}
		},
		mounted() {
			//id
			this.ObjectCon();
		},
		computed:{
			//
			imgUrl(){
				return 'https://zht.insooner.com/jg/v1/getUpdatePictures';   // 
			},
			imgData(){
				return {}  //
			},
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: calc(100% - 80px);
			padding-left: 80px;

			.inupdbox {
				width: calc(50% - 80px);
				padding-right: 80px;
				float: left;
			}
		}
	}
</style>



