<template>
	<div class="login">
		<div class="login-container"></div>
		<el-form :model="ruleForm" :rules="rules" status-icon ref="ruleForm" label-position="top" label-width="0px" class="demo-ruleForm login-page" v-if="$route.query.id==2">
			<h3 class="title"></h3>
			<el-form-item label="" prop="cname">
				<el-input size="large" v-model="ruleForm.cname" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="province">
				<el-cascader style="width: 100%;" size="large" :options="$regionData"  v-model="selectedOptions" @change="addressChange">
				</el-cascader>
			</el-form-item>
			<el-form-item label="" prop="address">
				<el-input size="large" v-model="ruleForm.address" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="nickname">
				<el-input size="large" v-model="ruleForm.nickname" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="phone">
				<el-input size="large" v-model="ruleForm.phone" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="organization">
				<el-input size="large" v-model="ruleForm.organization" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="mlicense">
				<el-upload
				  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
				  list-type="picture-card"
				  :file-list="ruleForm.mlicense"
				  :on-preview="handlePictureCardPreview"
				  :on-success="handsuccess"
				  :on-remove="handleRemove">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</el-form-item>
			<el-form-item label="" prop="remarks">
				<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder=""></el-input>
			</el-form-item>
			<el-form-item style="width:100%;">
				<el-button type="danger" round style="width:100%; height: 40px;" @click="submitForm('ruleForm')"></el-button>
			</el-form-item>
		</el-form>
		<div class="pagesh" v-else-if="$route.query.id==3">
			<h1 style="color: #1989FA;"></h1>
			<div style="margin: 30px 0;">24</div>
			<div>400-006-0996</div>
		</div>
		<div class="pagesh" v-else-if="$route.query.id==4">
			<h1></h1>
			<div style="margin: 30px 0;">400-006-0996</div>
			<!-- <div style="margin: 0 0 30px 0;"></div> -->
			<el-button class="pageshbutton" type="success" round @click="pageshsh()"></el-button>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				// 
				dialogImageUrl: '',
				dialogVisible: false,
				// 
				selectedOptions: [],
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {
					cname:'',
					province:'',
					address:'',
					nickname:'',
					phone:'',
					organization:'',
					remarks:''
				},
				rules: {
					cname: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					address: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					nickname: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					organization: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					remarks: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			//
			handleRemove(file, fileList) {
				this.ruleForm.mlicense = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			},
			// 
			handsuccess(file, fileList){
				this.ruleForm.mlicense.push(fileList);
			},
			// 
			addressChange(arr) {
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			// 
			submitForm(formName) {
				let that = this;
				that.$refs[formName].validate((valid) => {
					if (valid) {
						// 
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;    
			      geocoder.getLocation(address); //
			      //
			      geocoder.setComplete(function(result) {
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 
						   let data = that.ruleForm;
						   that.$post('v1/updateMaterial',data).then((response) => {
						   	if (response.code==1000) {
						   		that.$message({
						   		  message: '',
						   		  type: 'success'
						   		});
								that.$router.push({path: '/InstinfoDL',query:{id:3}});
						   	};
						   })
						});	
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			pageshsh(){
				this.$router.push({path: '/InstinfoDL',query:{id:2}});
			}
		},
		mounted() {
		}
	}
</script>

<style scoped>
	.INzc{
		text-align: center;
	}
	.INzc span{
		padding: 0 0.625rem;
		cursor: pointer;
		color: #666666;
		font-size: 0.75rem;
	}
	.login{
		position: relative;
		width: 100%;
		height: 100%;
	}
	.login-container {
		width: 100%;
		height: 100%;
		top:0;
		left: 0;
		background-image: url(../assets/images/lodbanner.jpg);
		background-size: 100% 100%;
		position: fixed;
	}

	.login-page {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -300px;
		margin-top: -400px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 600px;
		padding: 35px 35px 80px;
		/* background: #fff; */
		/* border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6; */
	}

	.title {
		font-size: 30px;
	}

	label.el-checkbox.rememberme {
		margin: 0px 0px 15px;
		text-align: left;
	}
	.pagesh{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -300px;
		margin-top: -200px;
		-webkit-border-radius: 5px;
		border-radius: 5px;
		width: 600px;
		padding: 35px 35px 80px;
		text-align: center;
	}
	.pagesh h1{
		color:#F7B84F;
	}
</style>



