<template>
	<div class="home">
		<div class="homelift">
			<div class="homelift-top">
				<div class="homelift-top-header">
					<span class="homelift-top-header-text"></span>
					<!-- <span class="homelift-top-header-more"><i style="margin-left: 6px;" class="el-icon-arrow-right"></i></span> -->
				</div>
				<div class="homelift-top-con">
					<el-row>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one">{{conobjCon.user_num}}</span>
							  <div class="homelift-top-con-K"></div>
						  </div>
						  <div><span class="homelift-top-con-two"></span></div>
						  <div><span class="homelift-top-con-three"><i>{{conobjCon.experience_num}}</i></span></div>
						  <div style="margin-bottom:70px;"><span class="homelift-top-con-three"><i>{{conobjCon.user_num_pay}}</i></span></div>
					  </el-col>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one" style="background-color: #FA9B5B;">{{conobjCon.total_price}}</span>
							  <div class="homelift-top-con-K" style="background-color: #FA9B5B;"></div>
						  </div>
						  <div><span class="homelift-top-con-two"></span></div>
						  <div><span class="homelift-top-con-three"><i>{{conobjCon.getday}}</i></span></div>
						  <div style="margin-bottom:70px;"><span class="homelift-top-con-three"><i>{{conobjCon.getmonth}}</i></span></div>
					  </el-col>
					  <el-col :span="8">
						  <div style="position: relative;margin-top: 20px;">
							  <span class="homelift-top-con-one" style="background-color:#598BF3;">{{conobjCon.classroom_num}}</span>
							  <div class="homelift-top-con-K" style="background-color:#598BF3;"></div>
						  </div>
						  <div><span class="homelift-top-con-two"></span></div>
						  <div><span class="homelift-top-con-three"><i>{{conobjCon.class_num}}</i></span></div>
						  <!-- <div style="margin-bottom:70px;"><span class="homelift-top-con-three"><i>57</i></span></div> -->
					  </el-col>
					</el-row>
				</div>
			</div>
			<!--  -->
			<div class="homelift-buttom">
				<div class="homelift-buttom-header">
					<span class="homelift-buttom-header-text"></span>
					<!-- <span class="homelift-buttom-header-more"><i class="el-icon-more"></i></span> -->
				</div>
				<div class="homelift-buttom-con">
					<el-row :gutter="20">
					  <el-col :span="16">
						<div class="homelift-buttom-con-nav">
							<span :class="navstate==1?'navstate':''" @click="navstatecli(1,'')"></span>
							<span :class="navstate==2?'navstate':''" @click="navstatecli(2,'')"></span>
							<span :class="navstate==3?'navstate':''" @click="navstatecli(3,'')"></span>
						</div>
						<div class="homelift-buttom-con-each">
							<div id="main" style="width:100%;height:350px;margin-bottom: 43px;"></div>
						</div>
					  </el-col>
					  <el-col :span="8">
						  <div class="homelift-buttom-con-right1">
							  <span></span>
							   <!-- <el-button round style="float: right;margin: 14px 30px 0 0;"></el-button> -->
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;"></span>
								  <!-- <span style="float: right;margin-right: 30px;"></span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #000;">{{conobjshuobj.month_income}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">12</span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;"></span>
								  <!-- <span style="float: right;margin-right: 30px;"></span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #F74F77;">{{conobjshuobj.month_Settlement}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;"></span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;"></span>
								  <!-- <span style="float: right;margin-right: 30px;"></span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color: #F7B84F;">{{conobjshuobj.month_refund}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">12</span> -->
							  </div>
						  </div>
						  <div class="homelift-buttom-con-right2">
							  <div class="homelift-buttom-con-right2-text">
								  <span style="float: left;"></span>
								  <!-- <span style="float: right;margin-right: 30px;"></span> -->
							  </div>
							  <div class="homelift-buttom-con-right2-mud">
								  <span style="float: left; color:#6B5AF4;">{{conobjshuobj.month_recharge}}</span>
								  <!-- <span style="float: right;margin-right: 30px;font-size: 16px;">10%</span> -->
							  </div>
						  </div>
					  </el-col>
					</el-row>
				</div>
			</div>
		</div>
		<!--  -->
		<div class="homeright">
			<homeright></homeright>
		</div>
	</div>
</template>

<script>
	import homeright from "./homeright.vue"; //
	var echarts = require('echarts');
	export default {
		components:{
			homeright
		},
	    data() {
	      return {
			navstate:1,
			navname:'',
			conobjCon:[],
			conobjshuobj:[]
	      };
	    },
	    methods: {
		  //
		  conobj(){
			  let that =this;
			  let data={};
			  that.$post('v1/getjgIndexStatistics',data).then((response) => {
			  	if (response.code==1000) {
			  		this.conobjCon = response.data
			  	};
			  });
		  },
		  conobjshu(){
			  let that =this;
			  let data={};
			  that.$post('v1/getjgIndexFinance',data).then((response) => {
				if (response.code==1000) {
					this.conobjshuobj = response.data
				};
			  });
		  },
		  titconobj(){
			  let that =this;
			  let data={
				  img_type:this.navstate
			  };
			  that.$post('v1/getjgIndexStatisticsImg',data).then((response) => {
				if (response.code==1000) {
					that.echartsTB(response.data);
				};
			  });
		  },
	      navstatecli(e,name){
			  //
			  this.navstate = e;
			  this.navname = name;
			  this.titconobj();
		  },
		  // 
		  echartsTB(datatit){
			  let that =this;
			  // domecharts
			  var myChart = echarts.init(document.getElementById('main'));
			  // 
			  myChart.setOption({
			      color: ['#3398DB'],
			          tooltip : {
			              trigger: 'axis',
			              axisPointer : {            // 
			                  type : 'shadow'        // 'line' | 'shadow'
			              }
			          },
			          grid: {
			              left: '3%',
			              right: '4%',
			              bottom: '3%',
			              containLabel: true
			          },
			          xAxis : [
			              {
			                  type : 'category',
			                  data : datatit.day,
			                  axisTick: {
			                      alignWithLabel: true
			                  }
			              }
			          ],
			          yAxis : [
			              {
			                  type : 'value'
			              }
			          ],
			          series : [
			              {
			                  name:that.navname,
			                  type:'bar',
			                  barWidth: '60%',
			                  data:datatit.income,
							  barWidth : 18,//
							  itemStyle: { //
								normal: {
									color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
										offset: 1,
										color: "#FFE57D" // 0% 
									},{
										offset:0,
										color: "#F8BA51" // 100% 
									}], false),
									barBorderRadius:[5, 5, 0, 0]
								}
							},
			              }
			          ]
			  });
		  }
	    },
		mounted() {
			this.conobj();
		    this.titconobj();
			this.conobjshu();
		},
	  }
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less';//
	// 
	.homelift{
		width: calc(68% - 30px);
		float: left;
		padding-right: 30px;
		margin-bottom: 30px;
		.homelift-top{
			width: 100%;
			background:@bgcolor;
			border:1px solid @bordersolid;
			border-radius:10px;
			.homelift-top-header{
				width:calc(100% - 40px);
				padding: 20px 20px;
				.homelift-top-header-text{
					border-left:@borderliftsolid;
					padding-left:6px;
					font-size: 16px;
				}
				.homelift-top-header-more{
					float: right;
					color: @color4;
					font-size: 16px;
					cursor: pointer;
				}
			}
			.homelift-top-con{
				width: 100%;
				text-align:center;
				margin-top: 30px;
				.homelift-top-con-one{
					background-color: #F4C75A;
					color: #fff;
					font-size: 32px;
					border-radius: 12px;
					padding: 4px 18px;
				}
				.homelift-top-con-two{
					font-size: 16px;
					line-height:60px;
					margin-bottom: 40px;
				}
				.homelift-top-con-three{
					font-size: 16px;
					color:@color4;
					line-height: 30px;
					i{
						color:@color5;
						font-style: normal;
					}
				}
			}
		}
	}
	.homelift-top-con-K{
		position:absolute;
		width: 12px;
		height: 12px;
		background:#F4C75A;
		left: 50%;
		margin-left: -6px;
		bottom: -6px;
		transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
	}
	
	// 
	.homelift-buttom{
		width: 100%;
		background:@bgcolor;
		border:1px solid @bordersolid;
		border-radius:10px;
		margin-top: 30px;		
		.homelift-buttom-header{
			width:calc(100% - 40px);
			padding: 20px 20px;
			.homelift-buttom-header-text{
				padding-left:6px;
				font-size: 16px;
				border-left:@borderliftsolid;
			}
			.homelift-buttom-header-more{
				float: right;
				color: @color4;
				font-size: 16px;
				cursor: pointer;
				i{
					font-size: 20px;
				}
			}
		}
		.homelift-buttom-con{
			width: 100%;
			text-align:center;
			.homelift-buttom-con-nav{
				width: 100%;
				span{
					margin: 0 20px;
					cursor: pointer;
					line-height: 60px;
				};
				.navstate{
					color: @color2;
					padding-bottom:10px;
					border-bottom: 2px @color2 solid;
				}
			}
			.homelift-buttom-con-each{
				width: 100%;
			}
			.homelift-buttom-con-right1{
				width: 100%;
				text-align: left;
				line-height: 60px;
				margin-bottom: 30px;
				span{
					font-size: 16px;
					color: @color4;
				}
			}
			.homelift-buttom-con-right2{
				margin-bottom: 20px;
				.homelift-buttom-con-right2-text{
					font-size: 16px;
					color: @color4;
					overflow: hidden;
				}
				.homelift-buttom-con-right2-mud{
					font-size: 30px;
					color: #999999;
					line-height: 40px;
					overflow: hidden;
				}
			}
		}
	};
	.homeright{
		width: 32%;
		float:right;
	}
</style>



