<template>
  <div class="InBoxBody">
    <div class="InBoxBody-header">
      <span class="InBoxBody-header-text"></span>
      <el-button class="buttonRight" round @click="handleEdit('1')"> </el-button>
    </div>
    <div class="InBoxBody-con">
      <div class="InBoxBody-con-input">
        <span></span>
        <el-input class="input" v-model="form.name" placeholder=""></el-input>
        <el-select class="input" v-model="form.mem_id" clearable placeholder="">
          <el-option v-for="item in $storeState.JGgllist" :key="item.mem_id" :label="item.cname" :value="item.mem_id">
          </el-option>
        </el-select>
        <el-button class="time" type="warning" plain @click="scIN()"> </el-button>
      </div>
      <!--  -->
      <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark"
        :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="cname" label="" align="center"></el-table-column>
        <el-table-column prop="sort" label="" align="center"></el-table-column>
        <el-table-column prop="c_num" label="" align="center"></el-table-column>
				<el-table-column prop="password" label="" align="center"></el-table-column>
        <el-table-column prop="name" label="" align="center"></el-table-column>
        <el-table-column label="" width="360" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="warning" @click="CopyKC(scope.row)"></el-button>
            <el-button size="mini" type="warning" @click="handleEdit('2',scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="success" @click="toClass(scope.$index, scope.row)"></el-button>
            <el-button size="mini" @click="handleOK(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="form.page"
          :page-size="16" layout="prev, pager, next, jumper" :total="num">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="top">
        <el-form-item label="" prop="name">
          <el-input size="large" v-model="ruleForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="large" type="number" v-model="ruleForm.sort" placeholder=":99"></el-input>
        </el-form-item>
				<el-form-item label="">
				  <el-input size="large" v-model="ruleForm.password" placeholder=""></el-input>
				</el-form-item>
        <el-form-item class="inupdbox" label="" prop="valkm">
        	<el-cascader
        	    size="large"
        		style="width: 100%;"
        	    v-model="ruleForm.valkm"
        	    :options="optionskm"
        	    :props="{ expandTrigger: 'hover' }">
        	</el-cascader>
        </el-form-item>
        <el-form-item label="(jpg/png400*400,2M):" prop="cover_img">
          <el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.cover_img"
            :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisibleimg">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <!--  -->
    <el-dialog title="" top="30vh" width="30%" :visible.sync="CopyKCidVisible">
      <el-select v-model="CopyKCidform.mem_id" style="width: 100%;" size="large" multiple placeholder="">
        <el-option v-for="item in $storeState.JGgllist" :key="item.mem_id" :label="item.cname" :value="item.mem_id">
        </el-option>
      </el-select>
      <div slot="footer" class="dialog-footer">
        <el-button @click="CopyKCidVisible = false"> </el-button>
        <el-button type="primary" @click="CopyKCcli()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        //
        num: 1,
        //id
        classid: null,
        //
        optionskm:[],
        //
        ruleForm: {
          name: '',
					password:'',
          valkm:'',
          cover_img: [],
          sort: ''
        },
        rules: {
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
			valkm: [{
			  required: true,
			  message: '',
			  trigger: 'blur'
			}],
          cover_img: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          sort: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        dialogVisible: false,
        dialogVisibleimg: false,
        dialogImageUrl: '',
        //
        form: {
          name: '',
		  mem_id:'',
          page: 1
        },
        tableData: [],
        //
        CopyKCidVisible: false,
        CopyKCidform: {
          mem_id: '',
          course_id: ''
        }
      }
    },
    methods: {
      //
      handleRemove(file, fileList) {
        this.ruleForm.cover_img = fileList;
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 
      handsuccess(file, fileList, fileimg) {
        this.ruleForm.cover_img = fileimg;
      },
      //
      CopyKCcli() {
        let that = this;
        that.$post('v1/copyjgCourseVideo', that.CopyKCidform).then((response) => {
          if (response.code == 1000) {
            that.CopyKCidVisible = false;
            //
            this.ObjAjax();
          };
        });
      },
      //
      CopyKC(item) {
        this.CopyKCidform.course_id = item.id;
        this.CopyKCidVisible = true;
        this.CopyKCidform.mem_id = '';
      },
      //
      toClass(index, row) {
        this.$router.push({
          path: 'Videolesson/Videolessonlist',
          query: {
            id: row.id,
		    mem_id:row.mem_id
          }
        });
      },
      //
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let url = 'v1/addjgCourseVideo';
            if (this.classid != null) {
              url = 'v1/setjgCourseVideo';
              this.ruleForm.course_id = this.classid
            };
            let that = this;
            let data = this.ruleForm;
            that.$post(url, data).then((response) => {
              console.log('', response);
              if (response.code == 1000) {
                that.$message({
                  type: 'success',
                  message: '!'
                });
                that.dialogVisible = false;
                // 
                that.ruleForm = {
                  name: '',
                  password:'',
                  valkm:'',
                  cover_img: [],
                  sort: ''
                };
                //
                that.ObjAjax();
              };
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //
      scIN() {
        this.form.page = 1;
        this.ObjAjax();
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.form.page = val;
        //
        this.ObjAjax();
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.form.page = val;
        //
        this.ObjAjax();
      },
      //
      handleEdit(state, index, row) {
        console.log(index, row);
        //
        if (state == '2') {
          this.classid = row.id;
          // 
          this.ruleForm= row;
        } else {
          // 
          this.ruleForm = {
            name: '',
            password:'',
            valkm:'',
            cover_img: [],
            sort: ''
          };
          this.classid = null;
        };
        this.dialogVisible = true;
      },
      //
      handleOK(index, row) {
        console.log(index, row);
        this.$confirm('' + row.name + ', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let that = this;
          let data = {
            course_id: row.id
          };
          that.$post('v1/deljgCourseVideo', data).then((response) => {
            console.log('', response);
            if (response.code == 1000) {
              that.$message({
                type: 'success',
                message: '!'
              });
              //
              that.ObjAjax();
            };
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleClose(done) {
        // 
        done();
      },
      //
      ObjAjax() {
        this.$storeState.Videolesson = this.form;
        let that = this;
        let data = this.form;
        that.$post('v1/getjgCourseVideo', data).then((response) => {
          console.log('', response);
          if (response.code == 1000) {
            this.tableData = response.data.info;
            this.num = response.data.num;
          };
          if (response.error_code == 10000) {
            that.tableData = [];
          };
        });
      },
      //
      objKm(){
      	let that =this;
      	that.$post('v1/getjggroupCategory').then((response) => {
      		if (response.code==1000) {
      			this.optionskm = response.data
      		};
      	});
      },
    },
    mounted() {
      this.ObjAjax();
      this.objKm();
    },
    created() {
      //
      if (this.$storeState.Videolesson != null) {
        this.form = this.$storeState.Videolesson;
      };
    }
  }
</script>

<style scoped lang="less">
  @import '../../../assets/style/index.less'; //

  .InBoxBody {
    width: 100%;
    border: @borderbox;
    background: @bgcolor;
    border: 1px solid @bordersolid;
    border-radius: 10px;
    overflow: hidden;

    .InBoxBody-header {
      width: calc(100% - 40px);
      padding: 20px 20px;

      .InBoxBody-header-text {
        padding-left: 6px;
        font-size: 16px;
        border-left: @borderliftsolid;
      }

      .buttonRight {
        float: right;
      }
    }

    .InBoxBody-con {
      width: 100%;
      overflow: hidden;

      .InBoxBody-con-input {
        width: 100%;
        overflow: hidden;
        margin-bottom: 20px;

        span {
          margin-left: 20px;
          color: @color4;
          font-size: 16px;
          float: left;
          line-height: 32px;
        }

        ;

        .input {
          float: left;
          width: 200px;
          height: 32px;
          margin-left: 12px;
        }

        ;

        .time {
          float: left;
          height: 32px;
          margin-left: 12px;
        }
      }

      ;

      .pagination {
        float: right;
        margin: 20px;
      }

      .logo {
        width: 50px;
        height: 50px;
        display: block;
        float: left;
        margin-top: 4px;
      }

      .logours {
        float: left;
        margin-left: 10px;
        width: 80px;
        text-align: left;

        div {
          line-height: 60px;
        }
      }
    }
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
    border: 1px dashed #d9d9d9;
  }

  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }

  .dialoginput {
    width: 100%;
    margin: 20px 0;
    overflow: hidden;

    .dialoginputFl {
      width: calc(50% - 10px);
      margin-right: 10px;
      float: left;
    }
  }
</style>



