<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<span class="InBoxBody-header-text"></span>
		</div>
		<div class="InBoxBody-con" v-if="is_verification==1">
			<el-form :model="ruleForm" label-position="top" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
				<el-form-item label="" prop="cname">
					<el-input size="large" v-model="ruleForm.cname" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="province">
					<el-cascader style="width: 100%;" size="large" :options="$regionData" :placeholder="ruleForm.province+'/'+ruleForm.city+'/'+ruleForm.area" v-model="selectedOptions" @change="addressChange">
					</el-cascader>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input size="large" v-model="ruleForm.address" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="nickname">
					<el-input size="large" v-model="ruleForm.nickname" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input size="large" v-model="ruleForm.phone" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="organization">
					<el-input size="large" v-model="ruleForm.organization" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="(3M)" prop="mlicense">
					<el-upload
					  multiple
					  action="https://zht.insooner.com/jg/v1/getUpdatePictures"
					  list-type="picture-card"
					  :file-list="ruleForm.mlicense"
					  :on-preview="handlePictureCardPreview"
					  :on-success="handsuccess"
					  :on-remove="handleRemove">
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
				<el-form-item label="" prop="remarks">
					<el-input type="textarea" :autosize="textst" v-model="ruleForm.remarks" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="InBoxBody-con" v-else-if="is_verification==3">
			<h1 style="color: #1989FA;"></h1>
			<div style="margin: 30px 0;">24</div>
			<div>400-006-0996</div>
		</div>
		<div class="InBoxBody-con" v-else-if="is_verification==4">
			<h1 style="color:#F7B84F;"></h1>
			<div style="margin: 30px 0;">400-006-0996</div>
			<!-- <div style="margin: 0 0 30px 0;"></div> -->
			<el-button class="pageshbutton" type="success" round @click="pageshsh()"></el-button>
		</div>
	</div>
</template>
 
<script>
	export default {
		data() {
			return {
				is_verification:2,
				// 
				dialogImageUrl: '',
				dialogVisible: false,
				// 
				selectedOptions: [],
				textst: {
					minRows: 5,
					maxRows: 6
				},
				ruleForm: {},
				rules: {
					cname: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					province: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					address: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					nickname: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					phone: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					organization: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					remarks: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			//
			handleRemove(file, fileList) {
				this.ruleForm.mlicense = fileList;
			},
			handlePictureCardPreview(file) {
			  this.dialogImageUrl = file.url;
			  this.dialogVisible = true;
			},
			// 
			handsuccess(url,file, fileList){
				this.ruleForm.mlicense = fileList;
			},
			// 
			addressChange(arr) {
				this.ruleForm.province = this.$CodeToText[arr[0]];
				this.ruleForm.city = this.$CodeToText[arr[1]];
				this.ruleForm.area = this.$CodeToText[arr[2]];
			},
			// 
			submitForm(formName) {
				let that = this;
				that.$refs[formName].validate((valid) => {
					if (valid) {
						// 
						var geocoder, map, marker = null;
						geocoder = new qq.maps.Geocoder();
						var address = that.ruleForm.province+that.ruleForm.city+that.ruleForm.area+that.ruleForm.address;    
			      geocoder.getLocation(address); //
			      //
			      geocoder.setComplete(function(result) {
						   that.ruleForm.latitude = result.detail.location.lat;
						   that.ruleForm.longitude = result.detail.location.lng;
						   // 
						   let data = that.ruleForm;
						   that.$post('v1/updateMaterial',data).then((response) => {
						   	if (response.code==1000) {
						   		that.$message({
						   		  message: '',
						   		  type: 'success'
						   		});
								that.$router.push({path:'/index'});
						   	};
						   })
						});	
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			pageshsh(){
				this.is_verification = 1;
			},
			//
			JGinfo(){
				let data={};
				this.$post('v1/getMemberMaterial',data).then((response) => {
					if (response.code==1000) {
						this.ruleForm = response.data;
						this.is_verification = response.data.is_verification
					};
				})
			},	
		},
		mounted() {
			this.JGinfo()
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}
		}

		.InBoxBody-con {
			width: 600px;
			margin-left: 30px;
			margin-bottom: 20px;
		}
	}
</style>



