import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'//
import Regist from '@/components/Regist'//
import index from '@/components/index'
import home from '@/components/AHome/home/home' //
//
import InstinfoDL from '@/components/Instinfo' //
//
import Dataanalysis from '@/components/BDataStat/Dataanalysis/Dataanalysis' //
import OrderAnalysis from '@/components/BDataStat/OrderAnalysis/OrderAnalysis' //
// 
import Student from '@/components/CEducAdmin/Student/Student' //
import Studdetails from '@/components/CEducAdmin/Student/Studdetails' //
import StuddetailsClass from '@/components/CEducAdmin/Student/StuddetailsClass' //
import evaluate from '@/components/CEducAdmin/evaluate/evaluate' //
//
import orderUnpaid from '@/components/DOrderAdmin/orderUnpaid/orderUnpaid' //
import orderUnpaiddetails from '@/components/DOrderAdmin/orderUnpaid/orderUnpaiddetails' //
import orderConduct from '@/components/DOrderAdmin/orderConduct/orderConduct' //
import orderConductdetails from '@/components/DOrderAdmin/orderConduct/orderConductdetails' //
import orderrefund from '@/components/DOrderAdmin/orderrefund/orderrefund' //
import orderrefunddetails from '@/components/DOrderAdmin/orderrefund/orderrefunddetails' //
import orderHistory from '@/components/DOrderAdmin/orderHistory/orderHistory' //
import orderHistorydetails from '@/components/DOrderAdmin/orderHistory/orderHistorydetails' //
import orderlesson from '@/components/DOrderAdmin/orderlesson/orderlesson' //
import orderlessondetails from '@/components/DOrderAdmin/orderlesson/orderlessondetails' //
// 
import ClassesAdmin from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdmin' //
import ClassesAdminCon from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdminCon' //
import coursemanagement from '@/components/ECourseAdmin/coursemanagement/coursemanagement' //
import CourGemCon from '@/components/ECourseAdmin/coursemanagement/CourGemCon' //
import CourGeD from '@/components/ECourseAdmin/coursemanagement/CourGeD' //
import Videolesson from '@/components/ECourseAdmin/Videolesson/Videolesson' //
import Videolessonlist from '@/components/ECourseAdmin/Videolesson/Videolessonlist' //
import VideolessonCon from '@/components/ECourseAdmin/Videolesson/VideolessonCon' //
//
import Classroom from '@/components/FOrgaAdmin/Classroom/Classroom' //
import ClassroomCon from '@/components/FOrgaAdmin/Classroom/ClassroomCon' //
import TeacherAdmin from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdmin' //
import TeacherAdminCon from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdminCon' //
//
import Experience from '@/components/GMarketing/Experience/Experience' //
import ExperienceCon from '@/components/GMarketing/Experience/ExperienceCon' //
import ExperienceD from '@/components/GMarketing/Experience/ExperienceD' //
import Secondkill from '@/components/GMarketing/Secondkill/Secondkill' //
import SecondkillCon from '@/components/GMarketing/Secondkill/SecondkillCon' //
import SecondkillD from '@/components/GMarketing/Secondkill/SecondkillD' //
//
import Complex from '@/components/HComplex/Complex/Complex' //
import ComplexCon from '@/components/HComplex/Complex/ComplexCon' //
// 
import FinaAnal from '@/components/YFinaAdmin/FinaAnal/FinaAnal' //
import BudgetData from '@/components/YFinaAdmin/BudgetData/BudgetData' //
// 
import Instinfo from '@/components/ZSystAdmin/Instinfo/Instinfo' //
import IntroCon from '@/components/ZSystAdmin/Introduction/IntroCon' //
import Introduction from '@/components/ZSystAdmin/Introduction/Introduction' //
import PrivAdmin from '@/components/ZSystAdmin/PrivAdmin/PrivAdmin' //
import SetUp from '@/components/ZSystAdmin/SetUp/SetUp' //

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
	routes: [{
			path: '*', //  
			redirect: '/Login'
		},{
			path: '/Login',
			name: '',
			component: Login,
		},{
			path: '/Regist',
			name: '',
			component: Regist,
		},
		{
			path: '/InstinfoDL',
			name: '',
			component: InstinfoDL,
		},{
		path: '/index',
		name: 'index',
		component: index,
		children: [{
				path: '/index',
				name: '18',
				component: home
			},
			//
			{path: '/Dataanalysis',name: '19',component: Dataanalysis},
			{path: '/OrderAnalysis',name: '20',component: OrderAnalysis},
			// 
			{path: '/Student',name: '21',component: Student},
			{path: '/Student/Studdetails',name: '21',component: Studdetails},
			{path: '/Student/StuddetailsClass',name: '21',component: StuddetailsClass},
			{path: '/evaluate',name: '21',component: evaluate},
			//
			{path: '/orderUnpaid',name: '22',component: orderUnpaid},
			{path: '/orderUnpaid/orderUnpaiddetails',name: '22',component: orderUnpaiddetails},
			{path: '/orderConduct',name: '23',component: orderConduct},
			{path: '/orderConduct/orderConductdetails',name: '23',component: orderConductdetails},
			{path: '/orderlesson',name: '24',component: orderlesson},
			{path: '/orderlesson/orderlessondetails',name: '24',component: orderlessondetails},
			{path: '/orderrefund',name: '25',component: orderrefund},
			{path: '/orderrefund/orderrefunddetails',name: '25',component: orderrefunddetails},
			{path: '/orderHistory',name: '26',component: orderHistory},
			{path: '/orderHistory/orderHistorydetails',name: '26',component: orderHistorydetails},
			// 
			{path: '/ClassesAdmin',name: '27',component: ClassesAdmin},
			{path: '/ClassesAdmin/ClassesAdminCon',name: '27',component: ClassesAdminCon},
			{path: '/coursemanagement',name: '28',component: coursemanagement},
			{path: '/coursemanagement/CourGemCon',name: '28',component: CourGemCon},
			{path: '/coursemanagement/CourGeD',name: '28',component: CourGeD},
      {path: '/Videolesson',name: '28',component: Videolesson},
      {path: '/Videolesson/VideolessonCon',name: '28',component: VideolessonCon},
      {path: '/Videolesson/Videolessonlist',name: '28',component: Videolessonlist},
			//
			{path: '/Classroom',name: '29',component: Classroom},
			{path: '/Classroom/ClassroomCon',name: '29',component: ClassroomCon},
			{path: '/TeacherAdmin',name: '29',component: TeacherAdmin},
			{path: '/TeacherAdmin/TeacherAdminCon',name: '29',component: TeacherAdminCon},
			// 
			{path: '/Experience',name: '30',component: Experience},
			{path: '/Experience/ExperienceCon',name: '30',component: ExperienceCon},
			{path: '/Experience/ExperienceD',name: '30',component: ExperienceD},
			{path: '/Secondkill',name: '31',component: Secondkill},
			{path: '/Secondkill/SecondkillCon',name: '31',component: SecondkillCon},
			{path: '/Secondkill/SecondkillD',name: '31',component: SecondkillD},
			//
			{path: '/Complex',name: 'Complex',component: Complex},
			{path: '/Complex/ComplexCon',name: 'ComplexCon',component: ComplexCon},
			// 
			{path: '/FinaAnal',name: '32',component: FinaAnal},
			{path: '/BudgetData',name: '37',component: BudgetData},
			// 
			{path: '/IntroCon',name: '33',component: IntroCon},
			{path: '/IntroCon/Introduction',name: '33',component: Introduction},
			{path: '/Instinfo',name: '34',component: Instinfo},
			{path: '/PrivAdmin',name: '9',component: PrivAdmin},
			{path: '/SetUp',name: '35',component: SetUp},
		]
	}]
})



